	module.exports = {"data":{"postsJson":{"title":"Live Godly Lives","questionsTitle":"COMPARISON, CONTRAST & REPETITION","display":"block","bookName":"2 Peter","ref":"1:1-3:18","html":"<p>   <strong>1</strong><u>1</u>Simon Peter, a servant and apostle of Jesus Christ, to those who have obtained a like precious faith with us in the righteousness of our God and Savior, Jesus Christ: <u>2</u>Grace to you and peace be multiplied in the knowledge of God and of Jesus our Lord, <u>3</u>seeing that his divine power has granted to us all things that pertain to life and godliness, through the knowledge of him who called us by his own glory and virtue; <u>4</u>by which he has granted to us his precious and exceedingly great promises; that through these you may become partakers of the divine nature, having escaped from the corruption that is in the world by lust. <u>5</u>Yes, and for this very cause adding on your part all diligence, in your faith supply moral excellence; and in moral excellence, knowledge; <u>6</u>and in knowledge, self-control; and in self-control patience; and in patience godliness; <u>7</u>and in godliness brotherly affection; and in brotherly affection, love. <u>8</u>For if these things are yours and abound, they make you to be not idle nor unfruitful to the knowledge of our Lord Jesus Christ. <u>9</u>For he who lacks these things is blind, seeing only what is near, having forgotten the cleansing from his old sins. <u>10</u>Therefore, brothers,* be more diligent to make your calling and election sure. For if you do these things, you will never stumble. <u>11</u>For thus you will be richly supplied with the entrance into the eternal Kingdom of our Lord and Savior, Jesus Christ.<br />   <u>12</u>Therefore I will not be negligent to remind you of these things, though you know them, and are established in the present truth. <u>13</u>I think it right, as long as I am in this tent, to stir you up by reminding you; <u>14</u>knowing that the putting off of my tent comes swiftly, even as our Lord Jesus Christ made clear to me. <u>15</u>Yes, I will make every effort that you may always be able to remember these things even after my departure. <u>16</u>For we did not follow cunningly devised fables, when we made known to you the power and coming of our Lord Jesus Christ, but we were eyewitnesses of his majesty. <u>17</u>For he received from God the Father honor and glory, when the voice came to him from the Majestic Glory, “This is my beloved Son, in whom I am well pleased.”* <u>18</u>We heard this voice come out of heaven when we were with him on the holy mountain.<br />   <u>19</u>We have the more sure word of prophecy; and you do well that you heed it, as to a lamp shining in a dark place, until the day dawns, and the morning star arises in your hearts: <u>20</u>knowing this first, that no prophecy of Scripture is of private interpretation. <u>21</u>For no prophecy ever came by the will of man: but holy men of God spoke, being moved by the Holy Spirit.<br />   <strong>2</strong><u>1</u>But false prophets also arose among the people, as false teachers will also be among you, who will secretly bring in destructive heresies, denying even the Master who bought them, bringing on themselves swift destruction. <u>2</u>Many will follow their immoral* ways, and as a result, the way of the truth will be maligned. <u>3</u>In covetousness they will exploit you with deceptive words: whose sentence now from of old doesn’t linger, and their destruction will not slumber. <u>4</u>For if God didn’t spare angels when they sinned, but cast them down to Tartarus,* and committed them to pits of darkness, to be reserved for judgment; <u>5</u>and didn’t spare the ancient world, but preserved Noah with seven others, a preacher of righteousness, when he brought a flood on the world of the ungodly; <u>6</u>and turning the cities of Sodom and Gomorrah into ashes, condemned them to destruction, having made them an example to those who would live ungodly; <u>7</u>and delivered righteous Lot, who was very distressed by the lustful life of the wicked <u>8</u>(for that righteous man dwelling among them, was tormented in his righteous soul from day to day with seeing and hearing lawless deeds): <u>9</u>the Lord knows how to deliver the godly out of temptation and to keep the unrighteous under punishment for the day of judgment; <u>10</u>but chiefly those who walk after the flesh in the lust of defilement, and despise authority. Daring, self-willed, they are not afraid to speak evil of dignitaries; <u>11</u>whereas angels, though greater in might and power, don’t bring a railing judgment against them before the Lord. <u>12</u>But these, as unreasoning creatures, born natural animals to be taken and destroyed, speaking evil in matters about which they are ignorant, will in their destroying surely be destroyed, <u>13</u>receiving the wages of unrighteousness; people who count it pleasure to revel in the daytime, spots and defects, reveling in their deceit while they feast with you; <u>14</u>having eyes full of adultery, and who can’t cease from sin; enticing unsettled souls; having a heart trained in greed; children of cursing; <u>15</u>forsaking the right way, they went astray, having followed the way of Balaam the son of Beor, who loved the wages of wrongdoing; <u>16</u>but he was rebuked for his own disobedience. A mute donkey spoke with a man’s voice and stopped the madness of the prophet. <u>17</u>These are wells without water, clouds driven by a storm; for whom the blackness of darkness has been reserved forever. <u>18</u>For, uttering great swelling words of emptiness, they entice in the lusts of the flesh, by licentiousness, those who are indeed escaping from those who live in error; <u>19</u>promising them liberty, while they themselves are bondservants of corruption; for a man is brought into bondage by whoever overcomes him.<br />   <u>20</u>For if, after they have escaped the defilement of the world through the knowledge of the Lord and Savior Jesus Christ, they are again entangled in it and overcome, the last state has become worse for them than the first. <u>21</u>For it would be better for them not to have known the way of righteousness, than, after knowing it, to turn back from the holy commandment delivered to them. <u>22</u>But it has happened to them according to the true proverb, “The dog turns to his own vomit again,”* and “the sow that has washed to wallowing in the mire.”<br />   <strong>3</strong><u>1</u>This is now, beloved, the second letter that I have written to you; and in both of them I stir up your sincere mind by reminding you; <u>2</u>that you should remember the words which were spoken before by the holy prophets, and the commandments of us, the apostles of the Lord and Savior: <u>3</u>knowing this first, that in the last days mockers will come, walking after their own lusts, <u>4</u>and saying, “Where is the promise of his coming? For, from the day that the fathers fell asleep, all things continue as they were from the beginning of the creation.” <u>5</u>For this they willfully forget, that there were heavens from of old, and an earth formed out of water and amid water, by the word of God; <u>6</u>by which means the world that then was, being overflowed with water, perished. <u>7</u>But the heavens that now are, and the earth, by the same word have been stored up for fire, being reserved against the day of judgment and destruction of ungodly men. <u>8</u>But don’t forget this one thing, beloved, that one day is with the Lord as a thousand years, and a thousand years as one day. <u>9</u>The Lord is not slow concerning his promise, as some count slowness; but is patient with us, not wishing that any should perish, but that all should come to repentance. <u>10</u>But the day of the Lord will come as a thief in the night; in which the heavens will pass away with a great noise, and the elements will be dissolved with fervent heat, and the earth and the works that are in it will be burned up. <u>11</u>Therefore since all these things will be destroyed like this, what kind of people ought you to be in holy living and godliness, <u>12</u>looking for and earnestly desiring the coming of the day of God, which will cause the burning heavens to be dissolved, and the elements will melt with fervent heat? <u>13</u>But, according to his promise, we look for new heavens and a new earth, in which righteousness dwells.<br />   <u>14</u>Therefore, beloved, seeing that you look for these things, be diligent to be found in peace, without defect and blameless in his sight. <u>15</u>Regard the patience of our Lord as salvation; even as our beloved brother Paul also, according to the wisdom given to him, wrote to you; <u>16</u>as also in all of his letters, speaking in them of these things. In those, there are some things that are hard to understand, which the ignorant and unsettled twist, as they also do to the other Scriptures, to their own destruction. <u>17</u>You therefore, beloved, knowing these things beforehand, beware, lest being carried away with the error of the wicked, you fall from your own steadfastness. <u>18</u>But grow in the grace and knowledge of our Lord and Savior Jesus Christ. To him be the glory both now and forever. Amen.</p><h5>*2 Peter 1:10 The word for “brothers” here and where context allows may also be correctly translated “brothers and sisters” or “siblings.”<br />*2 Peter 1:17 Matthew 17:5; Mark 9:7; Luke 9:35<br />*2 Peter 2:2 TR reads “destructive” instead of “immoral”<br />*2 Peter 2:4 Tartarus is another name for Hell<br />*2 Peter 2:22 Proverbs 26:11</h5>","path":"/415","Question1":"To compare is to associate two or more similar persons, ideas, or situations. Can you describe any comparisons made by the author of this portion of Scripture? Why do you think that the author would have made this comparison?","Question2":"To contrast is to put side by side two or more distinct persons, ideas, or situations. Can you describe any contrasts made by the author of this story portion of Scripture? Why do you think that the author would have made this contrast?","Question3":"Take time to note any repeated words or concepts you have observed in this portion of Scripture.","Question4":"Please take time to post on your social media what you have learned through this passage.","prevPath":"/414","nextPath":"/416"}},"pathContext":{}}

